* {
  box-sizing: border-box;
}


body {
  background-color: papayawhip;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  margin: 0;
  align-items: center;
  justify-content: center;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 16px;
}

.container {
  width: 350px;
  margin: auto;
}

h1 {
  margin: 0;
  letter-spacing: 1px;
}

h3 {
  margin-top: 40px;
  margin-right: 0px;
  margin-bottom: 10px;
  margin-left: 0px;
  padding-bottom: 10px;
  border-bottom: 1px solid;
  border-color: lightgray;
}

h4 {
  margin: 0;
  text-transform: uppercase;
}

.incomeExpense {
  background-color: white;
  padding: 20px;
  margin: 20px 0;
  box-shadow: var(--box-shadow);
  display: flex;
  justify-content: space-between;
}

.incomeExpense div {
  text-align: center;
  flex: 1;
}

.incomeExpense div:first-of-type {
  border-right: 1px solid black;
}

.money {
  margin: 5px 0;
  font-size: 20px;
  letter-spacing: 1px;
}

.money.plus {
  color: green;
}

.money.minus {
  color: maroon;
}

label {
  margin: 10px 0;
  display: inline-block;
}

input[type='text'],
input[type='number'] {
  width: 100%;
  padding: 10px;
  font-size: 12px;
  border: 1px solid lightgray;
  border-radius: 2px;
  display: block;
}

.list {
  padding: 0;
  list-style-type: none;
  margin-bottom: 40px;
}

.list li {
  background-color: #f2f2f2;
  color: #666;
  padding: 10px;
  margin: 10px 0;
  display: flex;
  justify-content: space-between;
  position: relative;
  box-shadow: var(--box-shadow);
}

.list li.plus {
  border-right: 5px solid #1faa59;
}

.list li.minus {
  border-right: 5px solid rgb(128, 0, 0);
}

.delete-btn {
  position: absolute;
  top: 50%;
  left: 0;
  opacity: 0;
  background-color: #c32e22;
  border: 0;
  color: #fff;
  font-size: 20px;
  line-height: 20px;
  padding: 2px 5px;
  cursor: pointer;
}

.list li:hover .delete-btn {
  opacity: 1;
}

.btn {
  width: 100%;
  padding: 10px;
  margin: 10px 0 30px;
  font-size: 16px;
  background-color: #7a6be0;
  color: #fff;
  border: 0;
  cursor: pointer;
  box-shadow: var(--box-shadow);
  display: block;
}

.btn:focus,
.delete-btn:focus {
  outline: 0;
}
